import React, { lazy, useEffect, useState } from 'react'
import Select from 'react-select';
import {
  CCol,
  CSelect,
  CCollapse,
  CModal,
  CFormGroup,
  CInputCheckbox,
  CButton,
  CLabel,
  CInput,
  CProgress,
  CRow,
  CCallout,
  CDataTable,
  CTextarea,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CForm
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {subkomoditasgaram, subsubkomoditasgaram, stdmutugaram, hscodegaram, subkomoditasberas,subsubkomoditasberas,stdmutuberas,hscodeberas, subkomoditasikan, subsubkomoditasikan, stdmutuikan, hscodeikan} from './sumber.js'
import { useSelector } from 'react-redux';
import store from '../../redux/store'

const FormImpor = (props) => {
    const PI = useSelector(state=>state.PI)
    const [dataitem, setDataitem]=useState([])
    useEffect(() => {
        // Update the document title using the browser API
        if(PI == "PI Garam Bahan Baku dan Bahan Penolong Industri"){
            setDataitem([
                {
                    "no":1,
                    "nmdok":"Izin Usaha Industri",
                    "nodok":"",
                    "status":"Selesai"
                },
                {
                    "no":2,
                    "nmdok":"Surat pernyataan bermeterai cukup keterangan mengenai rencana Impor Garam",
                    "nodok":"",
                    "status":"Selesai"
                },  
                {
                    "no":3,
                    "nmdok":"Rencana Penyaluran/Distribusi/Penjualan produk yang diolah menggunakan garam",
                    "nodok":"",
                    "status":"Selesai"
                },
                {
                    "no":4,
                    "nmdok":"Rekomendasi dari Menteri Perindustrian atau Pejabat yang ditunjuk",
                    "nodok":"",
                    "status":"Selesai"
                }, 
                {
                    "no":5,
                    "nmdok":"NIB",
                    "nodok":"",
                    "status":"Selesai"
                },
                {
                    "no":6,
                    "nmdok":"Surat Izin Usaha Perdagangan",
                    "nodok":"",
                    "status":"Selesai"
                },                                                
            ])
        } else {
            setDataitem([
                {
                    "no":1,
                    "nmdok":"Surat pernyataan bermeterai cukup yang memuat keterangan mengenai rencana",
                    "nodok":"",
                    "status":"Selesai"
                },
                {
                    "no":2,
                    "nmdok":"Rekomendasi dari Menteri Kelautan dan Perikanan atau pejabat yang ditunjuk",
                    "nodok":"",
                    "status":"Selesai"
                },  
                {
                    "no":3,
                    "nmdok":"NIB",
                    "nodok":"",
                    "status":"Selesai"
                }                                               
            ])
        }
      },[]);
  return (
      <>
      <div>
          <h5>{PI}</h5>
      </div>
        <div>
        <CDataTable
        addTableClasses="josss"
        items={dataitem}
        fields={[
        { key: 'no',  label:"No" },      
        { key: 'nmdok', label:"Nama Dokumen" },
        { key: 'nodok',  label:"Nomor Dokumen" },      
        { key: 'status',  label:"Status" },
        { key: 'aksi',  label:"Aksi" },              
        ]}
        scopedSlots={{
            'aksi':
            (item)=>{
                return(
                    <td>
                        <CButton className="btn btn-sm" style={{backgroundColor:"#02265d", color:"white"}}><i class="fas fa-info-circle"></i></CButton>
                    </td>
                )
            }
        }}
        />
        </div>
      </>
  )
}

export default FormImpor
